

import React from 'react';
import PropTypes from 'prop-types';

const ResumePropTypes = require('../../prop_types/resume');


export class Skill  extends React.Component {
    state = {
        style: {
            background: '#313131'
        }
    }

    render() {
        const style = this && this.state.style

        return (
            <div className='row inside'>
                <h3>{this.props.title}</h3>
                <p className='skill-description'>{this.props.description}</p>
                <div className='bars'>
                    <ul className='skills'>
                        {this.props.keywords.map(function (entry, index) {
                            return (
                                <li key={index}>
                                    <span
                                        className={`bar-expand percentage90`}
                                        style={style}/>
                                    <em>{entry}</em>
                                </li>

                            );
                        })}
                    </ul>
                </div>
            </div>
        );
    }
};

Skill.propTypes = {
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    keywords: PropTypes.arrayOf(PropTypes.string).isRequired
}

export default class Skills  extends React.Component {
    render() {
        return (
            <section id='skill'>
                <div className='row skill'>
                    <div className='two columns header-col'>
                        <h1>
                            <span>Skills</span>
                        </h1>
                    </div>
                    <div className='ten columns main-col'>
                        {this.props.content.skills.map(function (skill, index) {
                            return (
                                <Skill
                                    key={index}
                                    title={skill.title}
                                    description={skill.description}
                                    keywords={skill.keywords}/>
                            );
                        })}
                        {/*
                            <Skill title='Languages' content={this.props.content.languages}/>
                        */}
                    </div>
                </div>
            </section>
        );
    }
};

Skills.propTypes = {
    content: PropTypes.shape({
        skills: ResumePropTypes.skillsSet,
        languages: ResumePropTypes.languagesSet
    }).isRequired
}

